@import "src/Scss/vars.scss";
@import "src/Scss/mixins.scss";

.footer {
  padding: 49px 0;
  border-top: 1px solid $footer-line;
  @include tablet-small-768 {
    justify-content: center;
  }
  &__container {
    @include flex-between;
    gap: 15px;
    @include tablet-small-768 {
      flex-direction: column;
      gap: 35px;
    }
  }
  &__copyright {
    @include text($ff, 1.6rem, 3.6rem, 600, $color-grey);
    letter-spacing: -0.5px;
  }
  &__span {
    color: #ececec;
  }
  &__links {
    display: flex;
    gap: 43px;
    svg {
      @include press;
      transition: $transition;
    }
    a {
      @include text($ff, 16px, 19px, 400, $color-white);
      transition: $transition;
    }
  }
}
@media (hover: hover) {
  .footer__links a {
    &:hover {
      svg {
        transform: scale(1.1);
      }
      svg path {
        fill: $color-primary;
      }
    }
  }
}
